  componentDidMount() {
    let list = [];
    let that = this;
    const xhr = new XMLHttpRequest();
    xhr.open('GET', 'https://justgonsk.azurewebsites.net/api/Test');
    xhr.setRequestHeader('Content-Type', 'application/json');
    xhr.send();

    xhr.onreadystatechange = function() {
      if (this.readyState != 4) return;
      if (this.status != 200) {
        alert( 'ошибка: ' + (this.status ? this.statusText : 'запрос не удался') );
        return;
      }
      list = JSON.parse(this.responseText);
      console.log(list);
      let i = 0;
      let prom = new Promise((res, rej) => {
        let cards = list.results.map.call(List, (val) => {
          return  <Card
                    key={i++}
                    src={val.images[0].image}
                    title={val.title.toUpperCase()}
                    date={val.dates[0].start_date ? val.dates[0].start_date.replace(/\-/g, '.'): null}
                    description={val.description.replace('<p>', '').replace('</p>', '')}
                  />
        });
        if(cards.length) res(cards);
      });


      prom.then(val => {
        console.log(val);
        that.setState({cards: val});
      });

    }
  }

